.feedback {
  margin-top: 140px;
  position: relative;
  min-height: 455px;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  padding-right: 0;

  &::before {
    content: "";
    position: absolute;
    background-image: url("../img/photo-sunset.jpg");
    background-repeat: no-repeat;
    background-color: #fe7865;
    width: 845px;
    height: 100%;
    top: 0;
    left: 27px;
    z-index: -1;
  }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @media (max-width: $bp-tablet) {
    margin-top: 125px;
    justify-content: center;

    &::before {
      background-image: url("../img/photo-sunset-tablet.jpg");
      width: 768px;
      left: 0;
    }
  }

  @media (max-width: $bp-mobile) {
    margin-top: 25px;
    padding: 0;

    &::before {
      content: none;
    }
  }
}
