.offer {
  padding: 10px;
  padding-top: 0;

  &__title {
    margin: 0;
    font-weight: 600;
    font-size: 18px;
    color: #141517;
    line-height: 1.8;
  }

  &__description {
    margin: 0;
    margin-top: 22px;
    font-size: 14px;
    line-height: 1.75;
    color: rgba(20, 21, 23, 0.5);
  }

  @media (max-width: $bp-tablet) {
    padding: 0;
    margin-top: 15px;
    text-align: center;
    padding-top: 30px;
    border-top: 1px solid #d7d9df;
  }

  @media (max-width: $bp-mobile) {
    margin-top: 0;
    border-top: none;

    &__description {
      padding: 0 5px;
    }
  }
}
